import "../../assets/styles/list.scss";

import React, { ReactNode, useState } from "react";
import { connect } from "react-redux";
import * as actions from "../../redux/actions";
import { AiOutlineClose } from "react-icons/ai";
import uid from "uid";

function Wishlist(props: any) {
  const { wishList, products, manageWishlist, manageCart, cartList } = props;

  const initialItems = products.filter((item: any) =>
    Object.values(wishList).some((newItem) => newItem === item.id)
  );

  const [items, seItems] = useState(initialItems);

  const deleteItem = (id: number) => {
    const finalWishList = wishList.filter((item: any) => item !== id);
    manageWishlist(finalWishList);

    const itemsLeft = items.filter((item: { id: any }) => item.id !== id);
    seItems(itemsLeft);
  };

  const handleManageCart = (id: number, price: number) => {
    const obj = {
      id,
      price,
      amount: 1,
    };

    const finalCartList =
      cartList.length > 0 && cartList.some((item: any) => item.id === id)
        ? [...cartList].map((item: any) => {
            return item.id === id ? obj : item;
          })
        : [...cartList, obj];

    manageCart(finalCartList);
    deleteItem(id);
  };

  const headers: string[] = ["product", "price", "buy"];

  const renderItems: any = items.map(
    (item: {
      id: number;
      images: string[];
      title: string;
      sale: number;
      price: number;
    }) => (
      <div key={uid()} className="list__item">
        <div className="list__box">
          <img
            src={item.images[0]}
            alt={`item.title`}
            className={`list__image`}
          />
          <h2 className="list__title">{item.title}</h2>
        </div>
        <div className="list__box">
          <p className="list__pricing">
            <strong className={`list__price`}>${item.price}</strong>
          </p>
          <button
            type="button"
            className="list__button"
            onClick={() => handleManageCart(item.id, item.price)}
          >
            add to cart
          </button>
        </div>
        <AiOutlineClose
          className={`list__close`}
          onClick={() => deleteItem(item.id)}
        />
      </div>
    )
  );

  const newHeaders = headers.map((item: React.ReactNode) => (
    <h2 key={uid()} className={`wishlist__heading`}>
      {item}
    </h2>
  ));

  return (
    <main className={`list`}>
      {wishList > 0 ? (
        <>
          <div className="list__head">
            <span></span>
            {newHeaders}
          </div>
          <div className="list__body">{renderItems}</div>
        </>
      ) : (
        <strong className="list__text">The wishlist has no items</strong>
      )}
    </main>
  );
}

export default connect((state) => state, actions)(Wishlist);
