import "./index.scss";

import React, { useState } from "react";
import { connect } from "react-redux";
import Item from "./Item";
import Items from "./Items";
import Toolbar from "./Toolbar";
import * as actions from "../../redux/actions";

function Bestseller(props: any) {
  const [category, setCategory] = useState("");
  const { products } = props;
  const usedProducts = category ? products.filter((item: { category: string; }) => item.category === category) : products;
  const getRundom = () => {
    return Math.floor(Math.random() * (usedProducts.length - 0 + 1)) + 0;
  };
  const getRundomNums = () => {
    let newRandom: string | any[] = [];
    while (newRandom.length <= 5) {
      let randomnumber = getRundom();
      newRandom = [...newRandom, usedProducts[randomnumber]];
    }
    return newRandom;
  };

  const printedProducts = getRundomNums().filter((item) => item);
  const product = products[getRundom()];

  return (
    <section className="bestseller">
      <div className="bestseller__wrapper">
        <div className="bestseller__top">
          <h2 className="bestseller__title">Bestsellers</h2>
          <Toolbar setCategory={setCategory} />
        </div>
        <div className="bestseller__container">
          {products.length > 1 && <Item product={product} />}
          {products.length > 1 && <Items products={printedProducts} />}
        </div>
      </div>
    </section>
  );
}

// export default Bestseller;
export default connect((state) => state, actions)(Bestseller);
