import "../../assets/styles/list.scss";

import React, { useState } from "react";
import { connect } from "react-redux";
import * as actions from "../../redux/actions";
import { AiOutlineClose } from "react-icons/ai";
import uid from "uid";

function Cart(props: any) {
  const { products, cartList, manageCart } = props;

  const initialItems = cartList.map((item: { id: any; amount: any }) => {
    const newItem = products.find((el: { id: any }) => el.id === item.id);
    newItem.amount = item.amount;
    return newItem;
  });

  const [items, seItems] = useState(initialItems);

  const headers: string[] = ["product", "price", "buy"];

  const setAmount = (newAmount: string, index: any) => {
    const newItems = [...items];
    newItems[+index].amount = +newAmount;
    manageCart(newItems);
  };

  const deleteItem = (id: any) => {
    const newItems = items.filter((item: { id: any }) => item.id !== id);
    const newCartItems = cartList.filter((item: { id: any }) => item.id !== id);

    manageCart(newCartItems);
    seItems(newItems);
  };

  const renderItems: any = items.map(
    (
      item: {
        id: number;
        title: string;
        price: number;
        sale: number;
        images: string[];
        amount: number;
      },
      index: any
    ) => (
      <div key={uid()} className="list__item">
        <div className="list__box">
          <img
            src={item.images[0]}
            alt={`item.title`}
            className={`list__image`}
          />
          <h2 className="list__title">{item.title}</h2>
        </div>
        <div className="list__box">
          <p className="list__pricing">
            <span className={`list__sale`}>${item.price}</span>
            <strong className={`list__price`}>
              ${item.price * item.amount}
            </strong>
          </p>
          <input
            type="number"
            className={`list__input`}
            value={item.amount}
            onChange={(event) => setAmount(event.target.value, index)}
          />
        </div>
        <AiOutlineClose
          className={`list__close`}
          onClick={() => deleteItem(item.id)}
        />
      </div>
    )
  );

  const newHeaders = headers.map((item: React.ReactNode) => (
    <h2 key={uid()} className={`wishlist__heading`}>
      {item}
    </h2>
  ));

  return (
    <main className={`list`}>
      {cartList.length > 0 ? (
        <>
          <div className="list__head">
            <span></span>
            {newHeaders}
          </div>
          <div className="list__body">{renderItems}</div>
        </>
      ) : (
        <strong className="list__text">The cart has no items</strong>
      )}
    </main>
  );
}

export default connect((state) => state, actions)(Cart);
