import "./index.scss";
import React from "react";
import { connect } from "react-redux";
import * as actions from "../../../redux/actions";

import { Select } from "antd";
const { Option } = Select;

function Filter(props: any) {
  const { setItemsPerPage, itemsPerPage } = props;

  return (
    <section className={`category-container__filter`}>
      <div className={`category-container__filter-left`}>
        <Select
          defaultValue={itemsPerPage}
          style={{ width: 120 }}
          onChange={(event) => setItemsPerPage(event)}
        >
          <Option value={5}>5</Option>
          <Option value={10}>10</Option>
          <Option value={15}>15</Option>
        </Select>
      </div>
    </section>
  );
}

export default connect((state) => state, actions)(Filter);
