import "./index.scss";

import { IoIosSearch } from "react-icons/io";
import { Select } from "antd";

import React, { useState } from "react";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import * as actions from "../../../../redux/actions";
import uid from "uid";

const { Option } = Select;

const Search = withRouter((props: any) => {
  const { products, history } = props;

  const [data, setData] = useState(products);
  const [value, setValue] = useState("");

  const onChange = (value: string) => {
    history.push(`/p/${value}`);
    setValue("");
  };

  const onSearch = (value: any) => {
    console.log(products);

    const result = products.filter((item: { title: any }) =>
      item.title.toLowerCase().includes(value.toLowerCase())
    );
    console.log(result);

    setData(result);
    setValue(value);
  };

  return (
    <div className={`search`}>
      <Select
        showSearch
        value={value}
        style={{ border: "none" }}
        placeholder="Search ..."
        optionFilterProp="children"
        onSearch={onSearch}
        onChange={onChange}
      >
        {data.map((item: any) => (
          <Option key={uid()} value={item.id}>
            {item.title}
          </Option>
        ))}
      </Select>
    </div>
  );
});

export default connect((state) => state, actions)(Search);
