import "./index.scss";

import React from "react";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";
import * as actions from "../../redux/actions";

const img =
  "https://elab.stylemixthemes.com/demo-6/wp-content/uploads/sites/9/2019/03/slider-banners-1920x550-07-1917x549.jpg";

const CatalogBanner = (props: any) => {
  const { match, categories } = props;
  const route = match.params.category;

  const bannerItem = categories.find(
    (item: { routes: string[]; mainLink: string }) =>
      item.routes.includes(route) || item.mainLink === route
  );

  return (
    <div
      className={`category-banner`}
      style={{ backgroundImage: `url(${bannerItem.image})` }}
    >
      <p className={`category-banner__text`}>Looking for more?</p>
      <h1 className={`category-banner__title`}>
        Get <span className={`category-banner__title--blue`}>$100</span> Voucher
        <span className={`category-banner__title--blue`}>*</span>
        <br />
        <span className={`category-banner__title--thin`}>
          {bannerItem.title}
        </span>
      </h1>
    </div>
  );
};

export default withRouter(CatalogBanner);
