import "./index.scss";

import React, { useState } from "react";
import "antd/dist/antd.css";
import Slider from "./Slider";
import Right from "./Right";

function Main(props: any) {
  const { product } = props;

  return (
    <div className={`product-main`}>
      {product && (
        <>
          <Slider product={product} />
          <Right product={product} />
        </>
      )}
    </div>
  );
}

export default Main;
