import "./index.scss";

import React from "react";
import Top from "./Top";
import Cart from "./Cart";
import Bottom from "./Bottom";

function Right(props: any) {
  const { product } = props;

  return (
    <div className={`product-main__right`}>
      {product && (
        <>
          <Top title={product.title} features={product.features}  />
          <Cart price={product.price} id={product.id} />
          <Bottom id={product.id} />
        </>
      )}
    </div>
  );
}

export default Right;
