import { GET_CATEGORIES, SET_ITEMS_PER_PAGE, SET_FILTERED_PRICE, MANAGE_CART, GET_PRODUCTS, MANAGE_WISHLIST, MANAGE_COMPARELIST, MANAGE_ROUTES, SET_FILTERED_ITEMS } from "../constants";

const initialCategories = {
  categories: [],
  selectedCategories: [],
  products: [],
  wishList: [],
  compareList: [],
  cartList: [],
  catalogRoutes: [],
  filteredItems: [],
  filteredPrice: [],
  itemsPerPage: 15,
};

type initialCategoriesType = typeof initialCategories;


export default (state: initialCategoriesType = initialCategories, action: any ) => {
  switch (action.type) {
    case GET_CATEGORIES:
      return {
        ...state,
        categories: action.categories,
        selectedCategories: action.selectedCategories,
      };
    case GET_PRODUCTS:
      return {
        ...state,
        products: action.products,
      };
    case MANAGE_CART:
      return {
        ...state,
        cartList: action.cartList,
      };
    case MANAGE_WISHLIST:      
        return {
          ...state,
          wishList: action.wishList,
        };
    case MANAGE_COMPARELIST:      
        return {
          ...state,
          wishList: action.compareList,
        };
// catalog
    case MANAGE_ROUTES:      
    return {
      ...state,
      catalogRoutes: action.catalogRoutes,
    };
    case SET_FILTERED_ITEMS:      
    return {
      ...state,
      filteredItems: action.filteredItems,
    };
    case SET_FILTERED_PRICE:      
    return {
      ...state,
      filteredPrice: action.filteredPrice,
    };
    case SET_ITEMS_PER_PAGE:      
    return {
      ...state,
      itemsPerPage: action.itemsPerPage,
    };
    default:
      return state;
  }
};

