import "./index.scss";

import React, { useState } from "react";
import uid from "uid";

function Top(props: any) {
  const { title, features } = props;

  const ItemList = Object.entries(features).map(([title, value]) => (
    <li key={uid()} className="product-main__top-item">
      {title}: {value}
    </li>
  ));

  return (
    <div className={`product-main__top`}>
      <h2 className="product-main__top-title">{title}</h2>
      <ul className="product-main__top-list">{ItemList}</ul>
    </div>
  );
}

export default Top;
