import "./index.scss";

import React, { useState } from "react";
import uid from "uid";
import { Tabs, Rate, Input } from "antd";
import Reviews from "./Reviews";
const { TabPane } = Tabs;
const { TextArea } = Input;

function ProductTabs(props: any) {
  const { product } = props;

const features = {
    Fit: "Over-Ear",
    "Noise Canceling": "Yes",
    Colors: "Blue, Matte Black, Red Shadow Gray, White, Blue",
    "Battery Life": "	Up to 22 hours with ANC Up to 40 hours without ANC",
    "Fast Fuel Charging": "Yes",
    Weight: "1.9 pounds",
    "BluetoothÂ® Wireless Connectivity*": "Yes",
    "AndroidTM Compatible": "Yes",
  };

  const additional = {
    Color: "Blue, Green, Red",
    Cable: "Yes, No",
  };

  const renderedDescription = () => {
    return product.description.map((item: string) => (
      <p key={uid()} className={`tabs-text`}>
        {item}
      </p>
    ));
  };
  const renderedFeatures = (features: any) => {
    return features ? Object.keys(features).map((key) => (
      <tr key={uid()} className={`tabs__tr`}>
        <td className={`tabs__td`}>{key}</td>
        <td className={`tabs__td`}>{features[key]}</td>
      </tr>
    )) : null;
  };

  return (
    <div className={`product-tabs`}>
      <Tabs defaultActiveKey="1">
        <TabPane tab="Description" key="1">
          {product && renderedDescription()}
        </TabPane>
        <TabPane tab="Main features" key="2">
          <table className={`tabs__table`}>
            <tbody>{product && renderedFeatures(product.features)}</tbody>
          </table>
        </TabPane>
        <TabPane tab="Reviews" key="3">
          <form action="" method="post" className={`tabs__form`}>
            <div className="tabs__input-group">
              <span>Your rating</span>
              <Rate />
            </div>
            <div className="tabs__input-group">
              <span>Your review *</span>
              <TextArea />
            </div>
            <div className="tabs__input-group">
              <span>Name*</span>
              <Input />
            </div>
            <div className="tabs__input-group">
              <span>Email*</span>
              <Input />
            </div>
            <div className="tabs__input-group">
              <span></span>
              <button type="submit" className={`tabs__form-button`}>
                submit review
              </button>
            </div>
          </form>
          <Reviews />
        </TabPane>
      </Tabs>
    </div>
  );
}

export default ProductTabs;
