import { GET_CATEGORIES, SET_FILTERED_PRICE, SET_FILTERED_ITEMS, MANAGE_CART, GET_PRODUCTS, MANAGE_WISHLIST, MANAGE_ROUTES, SET_ITEMS_PER_PAGE } from "../constants";


export const manageWishlist = (wishList: any) => {
  return {
    type: MANAGE_WISHLIST,
    wishList,
  };
};
export const manageCart = (cartList: any) => {
  return {
    type: MANAGE_CART,
    cartList,
  };
};
export const manageCompareList = (compareList: any) => {
  return {
    type: MANAGE_WISHLIST,
    compareList,
  };
};

export const getProducts = (data: any) => {
  return {
    type: GET_PRODUCTS,
    products: data,
  };
};

export const addCategories = (data: any, selectedCategories: any) => {
  
  return {
    type: GET_CATEGORIES,
    categories: data,
    selectedCategories
  };
};

export const manageRoutes = (catalogRoutes: any) => {
  return {
    type: MANAGE_ROUTES,
    catalogRoutes,
  };
};

export const setFilteredItems = (filteredItems: any) => {
  return {
    type: SET_FILTERED_ITEMS,
    filteredItems,
  };
};

export const setFilteredPrice = (filteredPrice: any) => {
  return {
    type: SET_FILTERED_PRICE,
    filteredPrice,
  };
};
export const setItemsPerPage = (itemsPerPage: any) => {
  return {
    type: SET_ITEMS_PER_PAGE,
    itemsPerPage,
  };
};

