import "./index.scss";

import React from "react";
import Timer from "./Timer/Item";

function Item(props: { product: any; }) {
  const { product } = props;

  return (
    <>
      <article className="bestseller__item">
        <div className="bestseller__item-top">
          <img
            src={product.images[0]}
            alt="headphones"
            className={`bestseller__image`}
          />
          <Timer />
        </div>
        <div className="bestseller__item-bottom">
          <h3 className={`bestseller__name`}>{product.title}</h3>
            <b className={`bestseller__price`}>${product.price}</b>
        </div>
      </article>
    </>
  );
}

export default Item;
