import "./index.scss";

import React, { useState, useEffect } from "react";
import { connect } from "react-redux";
import * as actions from "../../../redux/actions";
import "antd/dist/antd.css";
import { Slider } from "antd";

function FilterPrice(props: any) {
  const { products, catalogRoutes, setFilteredPrice } = props;

  const createFilteredItems = () => {
    const newProducts = catalogRoutes
      ? products.filter((item: { category: any }) =>
          catalogRoutes.some((el: any) => el === item.category)
        )
      : products;

    let filterItems: any[] = [];

    newProducts.forEach((item: { price: number }) => {
      if (!filterItems.some((el) => el.price === item.price)) {
        filterItems = [...filterItems, item.price];
      }
    });

    return filterItems;
  };
  const items = createFilteredItems();

  let min = items.length > 0 ? Math.min(...items) : 0;
  let max = items.length > 0 ? Math.max(...items) : 100;

  useEffect(() => {
    setBeginningIndex(min);
    setEndingindex(max);
  }, []);

  const [beginningIndex, setBeginningIndex] = useState(min);
  const [endingindex, setEndingindex] = useState(max);

  const setItems = (event: any) => {
    setBeginningIndex(event[0]);
    setEndingindex(event[1]);
    setFilteredPrice(defaultValue);
  };

  const defaultValue: [number, number] = [beginningIndex, endingindex];

  return catalogRoutes.length === 1 ? (
    <div className={`filter-price`}>
      <div className={`filter-price__slider`}>
        <Slider
          range
          defaultValue={defaultValue}
          min={min}
          max={max}
          onChange={(event) => setItems(event)}
        />
      </div>
      <div className={`filter-price__bottom`}>
        <div className="filter-price__text">
          Price:
          <span className="filter-price__range">
            ${beginningIndex} - ${endingindex}
          </span>
        </div>
      </div>
    </div>
  ) : null;
}

export default connect((state) => state, actions)(FilterPrice);
