import "./index.scss";

import React from "react";
import { connect } from "react-redux";
import * as actions from "../../../redux/actions";
import { IoIosHeartEmpty, IoIosHeart } from "react-icons/io";
import LogoSVG from "../../../assets/images/header/logo.svg";
import { Link } from "react-router-dom";

import Search from "./Search";
import uid from "uid";

function Top(props: any) {
  const { wishList } = props;

  return (
    <div className={`top`}>
      <Link key={uid()} to={`/`}>
        <img src={LogoSVG} alt="logo" />
      </Link>
      <Search />
      <div className={`top__right`}>
        <Link key={uid()} to={`/wishlist`}>
          {wishList.length > 0 ? (
            <IoIosHeart className={`top__icon`} />
          ) : (
            <IoIosHeartEmpty className={`top__icon`} />
          )}
        </Link>
      </div>
    </div>
  );
}

export default connect((state) => state, actions)(Top);
