import "./index.scss";

import React, { useState } from "react";
import { connect } from "react-redux";
import * as actions from "../../../../../redux/actions";

function Cart(props: any) {
  const { id, price, cartList, manageCart } = props;

  const [cart, setCart] = useState(1);

  const handleManageCart = () => {
    const obj = {
      id,
      price,
      amount: cart,
    };

    const finalCartList =
      cartList.length > 0 && cartList.some((item: any) => item.id === id)
        ? [...cartList].map((item: any) => {
            return item.id === id ? obj : item;
          })
        : [...cartList, obj];

    manageCart(finalCartList);
  };

  return (
    <div className={`product-main__cart`}>
      <span className={`product-main__cart-price`}>${price * cart}</span>
      <div className={`product-main__cart-right`}>
        <input
          type="number"
          value={cart}
          onChange={(event) => setCart(+event.target.value)}
          className={`product-main__cart-input`}
        />
        <button
          type="button"
          className={`product-main__cart-button`}
          onClick={() => handleManageCart()}
        >
          add to cart
        </button>
      </div>
    </div>
  );
}

export default connect((state) => state, actions)(Cart);
