import "./index.scss";

import React, { useState } from "react";
import { connect } from "react-redux";
import * as actions from "../../redux/actions";
import CustomButton from "../CustomButton";
import { withRouter, Link } from "react-router-dom";
import uid from "uid";

const HomeSlider = withRouter((props: any) => {
  const { categories, history } = props;

  const [current, setCurrentSlide] = useState(1);

  const points = categories.map((item: { order: number }) => (
    <li
      key={uid()}
      className={`home-slider__point ${
        item.order !== current
          ? "home-slider__point--ball"
          : "home-slider__point--number"
      }`}
      onClick={() => setCurrentSlide(item.order)}
    >
      {item.order === current && item.order}
    </li>
  ));

  const currentSlider = categories.find(
    (item: { order: number }) => item.order === current
  );

  const onClickButton = (value: string) => {
    history.push(`/c/${value}`);
  };

  return (
    <div className={`home-slider`}>
      <div className={`home-slider__container`}>
        {currentSlider && (
          <>
            <div className={`home-slider__left`}>
              <p className={`home-slider__text`}>
                {currentSlider && currentSlider.title}
              </p>
              <h1 className={`home-slider__title`}>
                {currentSlider && currentSlider.description}
              </h1>
              <div className={`home-slider__toolbar`}>
                <Link to={`/product/${currentSlider.routes[0]}`}></Link>
                <CustomButton
                  text="view all"
                  type="default"
                  handleClick={() => onClickButton(currentSlider.routes[0])}
                />
              </div>
            </div>
            <img
              src={currentSlider.slideImage}
              alt={currentSlider.title}
              className={`home-slider__image`}
            />
          </>
        )}
        <ul className={`home-slider__points`}>{points}</ul>
      </div>
    </div>
  );
});

export default connect((state) => state, actions)(HomeSlider);
