import "./index.scss";

import React, { useState } from "react";
import { connect } from "react-redux";
import * as actions from "../../../../redux/actions";
import { Link } from "react-router-dom";
import { AiOutlineShopping } from "react-icons/ai";
import uid from "uid";

function Price(props: any) {
  const { cartList } = props;

  const countAmount = () => {
    return cartList.reduce(
      (accum: any, item: { amount: any }) => accum + item.amount,
      0
    );
  };

  const countSum = () => {
    return cartList.reduce(
      (accum: number, item: { amount: number; price: number }) => {
        const amountItems = item.amount || 1;
        return accum + amountItems * item.price;
      },
      0
    );
  };

  const amount = cartList.length > 0 ? countAmount() : 0;
  const quantity = cartList.length > 0 ? countSum() : 0;

  return (
    <div className={`price`}>
      <div className={`price__icon`}>
        <Link key={uid()} to={`/cart`}>
          <AiOutlineShopping size={20} />
          <span className={`price__quantity`}>{amount}</span>
        </Link>
      </div>
      <span className={`price__amount`}>${quantity}</span>
    </div>
  );
}

export default connect((state) => state, actions)(Price);
