import "./index.scss";

import React, { useState } from "react";
import { connect } from "react-redux";
import * as actions from "../../redux/actions";
import Filter from "./Filter";
import { Pagination } from "antd";
import CatalogItems from "../CatalogItems";

function CatalogContainer(props: any) {
  const { products, catalogRoutes, filteredPrice, itemsPerPage } = props;
  const [currentPage, setCurrentPage] = useState(1);
  
  const productsByCategory =
    catalogRoutes.length > 0
      ? products.filter((item: { category: any }) =>
          catalogRoutes.some((el: any) => el === item.category)
        )
      : products;

  const productsByPrice =
    filteredPrice.length > 0
      ? productsByCategory.filter(
          (item: { price: any }) =>
            item.price >= filteredPrice[0] && item.price <= filteredPrice[1]
        )
      : productsByCategory;
      
      const min = currentPage > 1 ? (currentPage-1) * itemsPerPage : 0;
      const max = currentPage * itemsPerPage;

  const productsPerPage = productsByPrice.filter(
    (item: any, index: number) => index >= min && index <= max - 1
  );

  return (
    <>
      <section className={`category-container`}>
        {products.length > 0 && (
          <>
            <Filter />
            <CatalogItems products={productsPerPage} />
            <div className={`pagination`}>
              <Pagination
                current={currentPage}
                total={productsByPrice.length}
                pageSize={itemsPerPage}
                onChange={(event) => setCurrentPage(event)}
              />
            </div>
          </>
        )}
      </section>
    </>
  );
}

export default connect((state) => state, actions)(CatalogContainer);
