import "./index.scss";

import React from "react";

const CustomButton = (props: any) => {
  const { text, type = "default", handleClick } = props;
  return (
    <button
      className={`custom-button ${
        type !== "default" ? "custom-button--volumed" : "custom-button--default"
      }`}
      onClick={handleClick}
    >
      {text}
    </button>
  );
};

export default CustomButton;
